.form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 500px;
  height: 300px;
  border: 2px solid lightblue;
  border-radius: 10px;
  margin: 0 auto;
}

.label {
  text-align: center;
  font-size: 25px;
  font-weight: 700;
  color: rgb(85, 190, 226);
}

.input {
   border-color: lightblue;
   border-radius: 10px;
   margin: 20px 0px;
   height: 30px;
   width: 150px;
}

.button {
  width: 150px;
  height: 30px;
  border: none;
  border-radius: 10px;
  background-color: lightblue;
    color: rgb(33, 91, 110);
}
.button:hover,
.button:focus {
  background-color: rgb(85, 190, 226);
  color: white;
}